@import "../sass/variables";
@import "../sass/mixins";


.block {
  width: 100%;
  height: 60px;
  background-color: #f8f9fb;
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 992px) {
    display: none;
  }

  .icon {
    width: 60px;
    height: 60px;
    background-image: linear-gradient(135deg, #4f5261 0%, #383a45 50%);
    display: flex;
    justify-content: center;
    align-items: center;

    .iconNetwork {
      width: 60px;
      height: 60px;
      filter: hue-rotate(277deg);
    }
  }

  .container {
    height: 100%;
    display: flex;
    align-items: center;
    margin-right: 10px;

    .info {
      display: flex;
      align-items: center;
      padding-right: 10px;
      border-right: 1px solid gainsboro;

      .name {

      }
    }

    .unLog {
      padding-left: 10px;

      .btn {
        @include text($mainTextStyle, 1rem, 1.2);
        color: $primaryColor;
      }
    }
  }
}